import React, { Component } from "react";
import classnames from 'classnames';
import cls from './styles.module.scss';
import app_vars from '../../../appConfig/app_vars.scss';
import { Col } from "antd";
import Img from 'react-image';

import {
    UniRContainerVertical,
    UniRContainerHorizontal,
    UniRPageLayout,
    UniRContainer,
    UniRImage
} from '../../../common';

class BlogComp extends Component {

    constructor(props) {
        super(props);
    }

    formatDate = (data) => {
        
        try {
            var date = new Date(data);
            let dat = new Intl.DateTimeFormat('en-GB', { year: "numeric", month: "short", day: "2-digit" })
                .format(date);
            return dat;
        }
        catch (e) {
            console.log("bug bug " + data);
        }

        return null;
    }

    getShortDes = (data) => {
        var d = "";
        for (var i = 0; i < data.length; i++) {
            if (i > 150 && (data[i] == '.' || data[i] == ' ')) {

                return d;
            }
            else d += data[i];
        }
        return data;
    }


    render() {
        let {
            className,
            data
        } = this.props;

        if (data.ArticleShortDescription === null) data.ArticleShortDescription = 'empty';

        data.ArticleShortDescription = data.ArticleShortDescription.toString().length > 200
            ? this.getShortDes(data.ArticleShortDescription.toString()) + '...'
            : data.ArticleShortDescription.toString();

        let {
            ArticleContent,
            ArticleImages,
            ArticleSEOTitle,
            ArticleStatus,
            ArticleTitle,
            CreatedDate,
            IsPinned,
            LastModified,
            StaffID,
            ArticleAuthor
        } = data;

        let formatDate = this.formatDate(CreatedDate);
        return (
                <Col className={classnames(cls.divRoot, className)} span={24}>
                    <UniRContainerHorizontal className={classnames(cls.blogBox)}>
                        <div className={classnames("col-3", cls.blogThumbContainer)}>

                            <UniRImage  
                                className={classnames(cls.blogThumb)}
                                lightbox = {true}
                                src={ArticleImages}

                                />
                            
                            
                        </div>
                        <UniRContainerVertical className={classnames("col-20", cls.content)}>
                            <p className={cls.blogTitle}>
                                <a href="https://translate.google.com/?hl=vi">
                                    {ArticleTitle}
                                </a>
                            </p>
                            <p className={classnames(cls.author)}>
                                {ArticleAuthor}
                                <em className={classnames(cls.point)}></em>
                                {formatDate}
                            </p>
                            <p>
                                {data.ArticleShortDescription}
                            </p>
                        </UniRContainerVertical>
                    </UniRContainerHorizontal>

                </Col>
        );
    }
}

export default BlogComp;