import React, { Component } from "react";

import classnames from 'classnames';
import cls from './style.module.scss';
import app_vars from '../../appConfig/app_vars.scss';
import { QUERY_HELPER } from '../../helper/queryHelper';
import { QUERY, STATUS } from '../../constant';
import { connect } from "react-redux";
import {
    searchAct,
    showmoreAct
}
    from "../../actions/blog";

import {
    UniRPageLayout,
    UniRForm,
    UniRContainerHorizontal,
    UniRContainerVertical,
    UniRInput,
    UniRButton,
    UniRContainer,
    UniRDropdown
} from '../../common';

import { Divider } from 'antd';

import BlogComp from "./BlogComp";
import { post } from "../../helper/axiosHelper";
import { BACKEND_URL } from '../../appConfig/index';

const BLOG_TITLE = "Blog";
const ROW_PER_PAGE = 10;


class Blog extends Component {

    constructor(props) {
        super(props);
        this.state = {
            current: 0,
            totalRecords: 0,
            blogData: [],
            searchText: "",
            showTotalRecordsNoti: false,
            whereClause: ""
        }
    }

    componentDidMount() {
        this.onSearch();
    }

    onShowMoreButtonClick = () => {

        let current = this.state.current + 1;

        let whereClause = this.state.whereClause;

        this.props.showmoreAct(whereClause, current, ROW_PER_PAGE, (data) => {
            let d = JSON.parse(data);

            let blogData = JSON.parse(d.data);
            let totalRecords = d.totalRecords;


            let oldBlogData = this.state.blogData;
            oldBlogData.push(...blogData);
            this.setState({ current: current, blogData: oldBlogData, totalRecords: totalRecords });
        })
    }

    onChange = (page) => {
        this.setState({ current: page });
    }

    getShowMoreButton = (blogDataloaded, totalRecords, showmoreStatus) => {
        if (blogDataloaded >= totalRecords) return null;

        let loading = (showmoreStatus == STATUS.loading);

        let btnShowMore =
            (
                <div className={classnames(cls.btnShowMore, "col-3")}>
                    <UniRButton
                        onClick={this.onShowMoreButtonClick}
                        type="danger"
                        loading={loading}
                    >
                        Xem thêm
                    </UniRButton>
                </div>
            )

        return btnShowMore;
    }

    createWhereClause() {
        let searchText = this.state.searchText;
        var pr = [];
        pr.push({
            ConditionOperation: QUERY.GroupClauseOperation.Or,
            GroupOperation: QUERY.GroupClauseOperation.And,
            Conditions: [
                QUERY_HELPER.createWhereClauseCondition("ArticleContent", searchText, QUERY.WhereClauseOperation.Like),
                QUERY_HELPER.createWhereClauseCondition("ArticleShortDescription", searchText, QUERY.WhereClauseOperation.Like),
                QUERY_HELPER.createWhereClauseCondition("ArticleTitle", searchText, QUERY.WhereClauseOperation.Like),
            ],
        });
        var json = pr.length > 0 ? QUERY_HELPER.prepareWhereClauseGroup(pr) : '';
        return json;
    }

    onSearch = () => {

        let whereClause = this.createWhereClause();

        this.setState({ whereClause: whereClause }, () => {
            let showTotalRecordsNoti = (this.state.searchText.replace(/ /g, '').length > 0);

            this.props.searchAct(whereClause, 0, ROW_PER_PAGE, (data) => {
                let d = JSON.parse(data);

                let blogData = JSON.parse(d.data);
                let totalRecords = d.totalRecords;

                this.setState({
                    blogData: blogData,
                    totalRecords: totalRecords,
                    current: 0,
                    showTotalRecordsNoti: showTotalRecordsNoti,
                    whereClause: whereClause
                });
            })
        });
    }

    onSearchTextChange = (value) => {
        this.setState({ searchText: value });
    }

    getTotalRecordsText = () => {

        let totalRecords = this.state.totalRecords;
        let showTotalRecordsNoti = this.state.showTotalRecordsNoti;

        if (showTotalRecordsNoti == false) return null;


        let displayText = (totalRecords == 0 ? "Không tìm thấy kết quả nào"
            : "Tổng số kết quả : " + totalRecords);

        let totalRecordsText =
            (
                <div className={classnames(cls.totalRecordsNoti)}>
                    {displayText}
                </div>
            )

        return totalRecordsText;
    }

    render() {

        let {
            totalRecords,
            blogData,
            searchText 
        } = this.state;

        let {
            searchStatus,
            showmoreStatus
        } = this.props.blog;

        let blogs = blogData.map((value) => (
            <BlogComp data={value} key={value.ArticleID} />
        ));

        let totalRecordsText = this.getTotalRecordsText();

        let btnShowMore = this.getShowMoreButton(blogData.length, totalRecords, showmoreStatus);
        let searchLoading = (searchStatus == STATUS.loading);

        return (

            <UniRPageLayout>
                <UniRContainerVertical className={classnames(cls.container)}>

                    <UniRContainer nomargin nopadding>
                        <Divider orientation={"left"}>
                            <h1>{BLOG_TITLE}</h1>
                        </Divider>
                    </UniRContainer>


                    <UniRContainer nomargin nopadding >
                        <UniRContainerHorizontal>
                            <UniRInput
                                containerStyle={classnames("col-9")}
                                onChange={(value) => this.onSearchTextChange(value)}
                                value={searchText}
                            >

                            </UniRInput>
                            <UniRContainer nomargin >
                                <UniRButton
                                    title="Tìm kiếm"
                                    onClick={this.onSearch}
                                    type="primary"
                                    loading={searchLoading}
                                />

                            </UniRContainer>

                        </UniRContainerHorizontal>
                        {totalRecordsText}
                    </UniRContainer>

                    <UniRContainer paddingvertical>
                        {blogs}
                    </UniRContainer>

                    <UniRContainer nomargin nopadding>

                        {btnShowMore}

                    </UniRContainer>
                </UniRContainerVertical>
            </UniRPageLayout>

        );
    }
}

const mapStateToProps = state => {
    return {
        blog: state.blog
    }
}

const mapDispatchToProps = dispatch => {
    return {
        searchAct: (whereClause, indexPage, ROW_PER_PAGE, callbackSuccess) => {
            dispatch(searchAct(whereClause, indexPage, ROW_PER_PAGE, callbackSuccess))
        },
        showmoreAct: (whereClause, indexPage, ROW_PER_PAGE, callbackSuccess) => {
            dispatch(showmoreAct(whereClause, indexPage, ROW_PER_PAGE, callbackSuccess))
        }
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Blog);

